import React, { useState }  from 'react';

import PropTypes from 'prop-types';
import './skl-ds-card.scss';

import { Text } from './../../atoms/skl-ds-text/skl-ds-text';

export const Card = ({ state,textHeader}) => {

  const [cardState, setCardState] = useState(state);

  return <div className={`skl-ds-card skl-ds-card--${cardState}`}>
      <div className={`skl-ds-card-header ${cardState === 'alert' ? 'skl-ds-card-header__background--alert' : ''}`}>        
          <Text tag={'p'} children={textHeader} fontSize={'small'} color={colorText}/>
      </div>
  </div>
};

Card.propTypes = {
  state: PropTypes.oneOf(['start', 'alert', 'expired', 'end']),
};

Card.defaultProps = {
  state: 'start',
};
